<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info" v-if="products.length === 1">
        {{ products.length }} товар
      </span>
      <span class="content__info" v-else-if="products.length > 1 && products.length < 5">
        {{ products.length }} товара
      </span>
      <span class="content__info" v-else>
        {{ products.length }} товаров
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem :item="item" v-for="item in products" :key="item.productId"></CartItem>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numFormat }} ₽</span>
          </p>

          <router-link tag="button" :to="{ name: 'orderPage' }" class="cart__button button button--primery" type="submit"
            :disabled="products.length === 0">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>

import GetCartProducts from '@/components/mixins/GetCartProducts';
import CartItem from '@/components/CartItem.vue';
export default {
  mixins: [GetCartProducts],
  components: { CartItem }
}
</script>

