<!-- eslint-disable max-len -->
<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a href="#" @click.prevent="prev()" :class="{ 'pagination__link--disabled': page === 1 }"
        class="pagination__link pagination__link--arrow " aria-label="Предыдущая страница">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>

    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
      <a href="#" class="pagination__link " @click.prevent="paginate(pageNumber)"
        :class="{ 'pagination__link--current': pageNumber === page }">
        {{ pageNumber }}
      </a>
    </li>

    <li class="pagination__item">
      <a @click.prevent="next()" :class="{ 'pagination__link--disabled': pages === page }"
        class="pagination__link pagination__link--arrow" href="#" aria-label="Следующая страница">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
</ul>
</template>

<script>
export default {
  props: ['page', 'perPage', 'prodAmount'],
  model: {
    prop: 'page',
    event: 'paginate'
  },
  computed: {
    pages() {
      return Math.ceil(this.prodAmount / this.perPage)
    },

  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page)
    },
    prev(page = this.page) {
      this.$emit('paginate', page - 1)
    },
    next(page = this.page) {
      this.$emit('paginate', page + 1)
    }
  }
}
</script>
