<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click="checkValue(count -= 1)">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>
    <input type="text" v-model.number="inputValue" name="count" @input="checkValue(count)">
    <button type="button" aria-label="Добавить один товар" @click="checkValue(count += 1)">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      count: 1
    }
  },
  methods: {
    checkValue(amount) {
      if (this.count > 0) {
        this.$emit('change-count', amount)
      }
    }
  },
  computed: {
    inputValue: {
      get() {
        return this.count
      },
      set(value) {
        if (this.count >= 1) {
          this.count = value
        }
      }
    }
  }
}

</script>

<style scoped>
button {
  cursor: pointer;
}
</style>