import Vue from "vue";
import VueRouter from "vue-router";
import PageList from '@/pages/PageList'
import PageItem from '@/pages/PageItem'
import ErrorPage from '@/pages/ErrorPage'
import BasketPage from '@/pages/BasketPage'
import OrderPage from '@/pages/OrderPage'
import OrderInfoPage from '@/pages/OrderInfoPage'




Vue.use(VueRouter)


const routes = [
  { name: 'list', component: PageList, path: '/' },
  { name: 'product', component: PageItem, path: '/product/:id' },
  { name: 'basketPage', component: BasketPage, path: '/cart' },
  { name: 'orderPage', component: OrderPage, path: '/order' },
  { name: 'OrderPage', component: OrderInfoPage, path: '/order/:id' },
  { name: 'notFound', component: ErrorPage, path: '*' },
]


const router = new VueRouter({
  // mode: 'history',
  routes
})

export default router