<!-- eslint-disable max-len -->
<template>
  <li class="catalog__item">
    <router-link class="catalog__pic" :to="{name:'product',params:{id:product.id}}">
      <img :src="product.img" :alt="product.name">
    </router-link>
    <h3 class="catalog__title">
      <a href="#">
        {{ product.name }}
      </a>
    </h3>
    <span class="catalog__price">
      {{ product.price | numFormat }} ₽
    </span>
    <ul class="colors colors--black">
      <li class="colors__item" v-for="colors in product.colors">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" v-model="color" :value="colors" checked="">
          <span class="colors__value" style="border: 1px solid grey;" :style="{ backgroundColor: colors,}">
          </span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script>

import numFormat from '@/helpers/numberFormat';

export default {
  props: ['product'],

  data() {
    return {
      color: ""
    }
  },
  filters:{
    numFormat
  },
}
</script>