<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId" :colors.sync="filterColors" :allProducts="products"></ProductFilter>
      <section class="catalog">
        <Preload  v-if="productsLoading"></Preload>
        <div v-if="productsLoadingError">Произошла ошибка
          <button @click="loadAgain()" class="button">Попробовать еще раз</button>
        </div>
        <ProductList :products="products"></ProductList>
        <BasePagination v-model="page" :per-page="productPerPage" :prodAmount="prodAmount">
        </BasePagination>
      </section>

    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import Preload from '@/components/Preload.vue'

export default {

  components: {
    ProductList,
    BasePagination,
    ProductFilter,
    Preload
  },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColors: 0,
      page: 1,
      productsLoading: false,
      productsLoadingError: false,
      productPerPage: 4,
      productsData: null,

    }
  },
  computed: {
    products() {
      return this.productsData
        ? this.productsData.items.map(item => {
          return {
            ...item,
            img: item.image.file.url,
            colors: item.colors.map(item => item.code),
            name: item.title
          }
        })
        : []
    },
    prodAmount() {
      return this.productsData ? this.productsData.pagination.total : 0
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingError = false;
      clearTimeout(this.loadProductsTimer)
      this.loadProductsTimer = setTimeout(() => {
        axios.get(`https://vue-study.skillbox.cc/api/products`, {
          params: {
            page: this.page,
            limit: this.productPerPage,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            categoryId: this.filterCategoryId,
            colorId: this.filterColors,
          }
        })
          .then(response => {
            return this.productsData = response.data
          })
          .then(() => {
            this.productsLoading = false;
          })
          .catch(() => {
            this.productsLoading = false;
            this.productsLoadingError = true;
          })
      }, 500);

    },
    loadAgain() {
      this.loadProducts()
    }
  },
  created() {
    this.loadProducts()
  },
  watch: {
    page() {
      this.loadProducts()
    },
    filterPriceFrom() {
      this.loadProducts()
    },
    filterPriceTo() {
      this.loadProducts()
    },
    filterCategoryId() {
      this.loadProducts()
    },
    filterColors() {
      this.loadProducts()
    }
  }
};
</script>

<style >
.buttonError {
  background-color: gray;
  border-color: transparent;
  padding: 10px;
  color: black;
  cursor: pointer;
  margin-right: 2px;
}

.buttonError:hover {
  opacity: 0.7;
}

</style>