<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'list' }" class="breadcrumbs__link">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'basketPage' }" class="breadcrumbs__link">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ products.length }} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">

            <BaseFormText v-model="formData.name" :error="formError.name" title="ФИО"
              placeholder="Введите ваше полное имя" name="name" />

            <BaseFormText v-model="formData.address" :error="formError.address" title="Адресс"
              placeholder="Введите ваш адрес" name="address" />

            <BaseFormText v-model="formData.phone" :error="formError.phone" title="Телефон"
              placeholder="Введите ваш телефон" type="tel" name="phone" />

            <BaseFormText v-model="formData.email" :error="formError.email" title="Email" placeholder="Введите ваш Email"
              type="email" name="email" />

            <BaseFormTextarea v-model="formData.comment" :error="formError.comment" title="Комментарий к заказу"
              name="info" />


          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in products" :key="item.id">
              <h3>{{ item.product.title }}</h3>
              <b>{{ item.product.price | numFormat }} ₽</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ products.length }}</b> товара на сумму <b>{{ totalPrice | numFormat }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
          <p v-if="formSending">Идет отправка</p>
        </div>
        <div v-if="formGlobalError" class="cart__error form__error-block">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{formGlobalError}}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>


import BaseFormField from '@/components/BaseFormField'
import BaseFormText from '@/components/BaseFormText'
import BaseFormTextarea from '@/components/BaseFormTextarea'
import GetCartProducts from '@/components/mixins/GetCartProducts';
import axios from 'axios';
export default {
  components: {
    BaseFormField,
    BaseFormText,
    BaseFormTextarea
  },
    mixins: [GetCartProducts],
  data() {
    return {
      formData: {},
      formError: {},
      formGlobalError:'',
      formSending:false
    }
  },
  methods: {
    order() {
      this.formSending = true
      axios
      .post('https://vue-study.skillbox.cc/api/orders',{ 
          ...this.formData 
        },{
          params: {
            userAccessKey:this.$store.state.userAccessKey
          }
        })
        .then((resp)=>{
          this.formGlobalError = ''
          this.formError = {}
          this.formData = {}
          this.$store.commit('resetCardProducts')
          this.$store.commit('updateOrderInfo', resp.data)
          this.formSending = false
          this.$router.push({name:'OrderPage',params:{id:resp.data.id}})
        })
        .catch((e)=>{
          this.formError = e.response.data.error.request || {},
          this.formGlobalError =  e.response.data.error.message || ''
        })
        
    }
  },

}
</script>