<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.img" width="120" height="120" :alt="item.product.name">
    </div>
    <h3 class="product__title">
      {{ item.product.name }}
    </h3>

    <span class="product__code">
      Артикул: {{ item.product.id }}
    </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" @click.prevent="decreaseAmount">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="amount" name="count">

      <button type="button" aria-label="Добавить один товар" @click.prevent="increaseAmount">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ item.product.price * item.amount | numFormat }}₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';
export default {
  props: ['item'],
  filters: {
    numFormat
  },
  computed: {
    amount: {
      get() {
        return this.item.amount
      },
      set(value) {
        if (value > 0) {
          this.$store.dispatch('changeCartItemAmount', { productId: this.item.productId, amount: value })
        }
        else {
          this.$store.dispatch('changeCartItemAmount', { productId: this.item.productId, amount: 1 })
        }
      }
    },

  },
  methods: {
    ...mapActions(['deleteCartItem']),
    deleteProduct() {
      this.deleteCartItem({ productId: String(this.item.product.id) })
    },
    decreaseAmount() {
      if (this.item.amount > 1) {
        this.$store.dispatch('changeCartItemAmount', { productId: this.item.productId, amount: this.item.amount - 1 })
      }
      else {
        this.$store.dispatch('changeCartItemAmount', { productId: this.item.productId, amount: 1 })
      }

    },
    increaseAmount() {
      this.$store.dispatch('changeCartItemAmount', { productId: this.item.productId, amount: this.item.amount + 1 })
    }
  }
}
</script>