import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";


Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo
    },
    resetCardProducts(state) {
      state.cartProducts = []
      state.cartProductsData = []
    },
    changeCartItemAmount(state, { productId, amount }) {
      const item = state.cartProducts.find(item => item.productId === productId)
      if (item) {
        item.amount = amount
      }
    },
    updateUserAccessKey(state, accesKey) {
      state.userAccessKey = accesKey
    },
    updateCartProductsData(state, cartProductsData) {
      state.cartProductsData = cartProductsData
    },
    syncCartData(state) {
      state.cartProducts = state.cartProductsData.map(item => {
        return {
          productId: item.product.id,
          amount: item.quantity
        }
      })
    }
  },
  getters: {
    cartDetailProduct(state) {
      return state.cartProducts.map(item => {
        const product = state.cartProductsData.find(p => p.product.id === item.productId).product
        return {
          ...item,
          product: {
            ...product,
            img: product.image.file.url
          }
        }
      })
    },
    getOrderInfo(state){
      return state.orderInfo || {}
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProduct.reduce((acc, item) => acc + item.product.price * item.amount, 0)
    }
  },
  actions: {
    loadOrderInfo(context, orderId){
      return axios
        .get('https://vue-study.skillbox.cc/api/orders/' + orderId, {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        })
        .then((resp)=>{
          context.commit('updateOrderInfo',resp.data)
        })
    },
    loadCart(context) {
      return axios
        .get('https://vue-study.skillbox.cc/api/baskets/', {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        })
        .then(resp => {
          if (!context.userAccessKey) {
            localStorage.setItem('userAccessKey', resp.data.user.accessKey)
            context.commit('updateUserAccessKey', resp.data.user.accessKey)
          }
          context.commit('updateCartProductsData', resp.data.items)
          context.commit('syncCartData')

        })
    },
    addProductToCart(context, { productId, amount }) {
      return axios
        .post('https://vue-study.skillbox.cc/api/baskets/products', {
          productId: productId,
          quantity: amount
        },
          {
            params: {
              userAccessKey: context.state.userAccessKey
            }
          })
        .then(resp => {
          context.commit('updateCartProductsData', resp.data.items)
          context.commit('syncCartData')
        })
    },
    changeCartItemAmount(context, { productId, amount }) {
      context.commit('changeCartItemAmount', { productId, amount })
      if (amount < 1) {
        return
      }
      return axios
        .put('https://vue-study.skillbox.cc/api/baskets/products', {
          productId: productId,
          quantity: amount
        },
          {
            params: {
              userAccessKey: context.state.userAccessKey
            }
          })
        .then(resp => {
          context.commit('updateCartProductsData', resp.data.items)

        })
        .catch(() => {
          context.commit('syncCartData')
        })
    },
    deleteCartItem(context, { productId }) {
      return axios
        .delete('https://vue-study.skillbox.cc/api/baskets/products',
          {
            data: {
              productId
            },
            params: {
              userAccessKey: context.state.userAccessKey
            },

          })
        .then(resp => {
          context.commit('updateCartProductsData', resp.data.items)
          context.commit('syncCartData')

        })
        .catch((e) => {
          console.log(e);
        })
    }
  }
})